    getCityData() {
      this.$http.get('https://gist.motc.gov.tw/gist_api/V3/Map/Basic/City?$format=JSON', getAxiosHeaders())
        .then((res) => {
          console.log(res.data);
          this.cityData = res.data;
        })
        .catch((err) => {
          console.dir(err);
        });
    },
    getData(page = 1) {
      console.log(page);
      const skipNum = (page - 1) * 30;
      const cityName = this.cityNameEn.split(' ').join('');
      this.$http.get(`https://ptx.transportdata.tw/MOTC/v2/Tourism/${this.type}/${cityName}?$top=30&$skip=${skipNum}&$format=JSON`, getAxiosHeaders())
        .then((res) => {
          console.log(res);
          this.data = res.data;
          this.$refs.pagination.getPagination(page);
          // this.getPagination(page);
        })
        .catch((err) => {
          console.dir(err);
        });
    },
    getAllData() {
      const cityName = this.cityNameEn.split(' ').join('');
      this.$http.get(`https://ptx.transportdata.tw/MOTC/v2/Tourism/${this.type}/${cityName}?$format=JSON`, getAxiosHeaders())
        .then((res) => {
          this.allData = res.data;
          this.allDataNum = res.data.length;
          // this.paginationData.pagesNum = Math.ceil(this.allDataNum / 30);
          this.getData();
        })
        .catch((err) => {
          console.dir(err);
        });
    },
    changeType(type) {
      this.type = type;
      this.getAllData();
    },
    renderSwiper() {
      this.otherCitySwiper = new Swiper(this.$refs.otherCitySwiper, {
        slidesPerView: 2.5,
        spaceBetween: 11,
        breakpoints: {
          // when window width is >= 430px
          768: {
            slidesPerView: 3,
            spaceBetween: 20,
          },
        },
      });
    },
    toDetailedPage(id) {
      this.$router.push(`/detailed/${id}`);
    },
    toCity(cityEn, cityName) {
      console.log(this.$router, cityEn);
      this.$router.push(`/city/${cityEn}?cityName=${cityName}`);
    },
