<template>
  <ul class="pagination d-flex justify-content-center">
    <li>
      <a class="d-flex justify-content-center align-items-center"
      :class="{'disabled': paginationData.current === 1}"
      href="javascript:;" @click="getData(1)">
        <span class="material-icons d-block">
          first_page
        </span>
      </a>
    </li>
    <li>
      <a class="d-flex justify-content-center align-items-center"
      :class="{'disabled': paginationData.current === 1}"
      href="javascript:;" @click="getData(paginationData.current - 1)">
        <span class="material-icons">
          chevron_left
        </span>
      </a>
    </li>
    <li :class="{'d-none': !paginationData.omitBefore}">...</li>
    <li v-for="item in paginationData.showPage"
    :key="`${item + ((paginationData.pageGroup - 1) * 4)}`">
      <a class="d-flex justify-content-center align-items-center"
      :class="{'active':
      item + ((paginationData.pageGroup - 1) * 4) === paginationData.current}"
      href="javascript:;" @click="getData(item + ((paginationData.pageGroup - 1) * 4))">
        {{item + ((paginationData.pageGroup - 1) * 4)}}
      </a>
    </li>
    <li :class="{'d-none': !paginationData.omitAfter}">...</li>
    <li>
      <a class="d-flex justify-content-center align-items-center"
      :class="{'disabled': paginationData.current === paginationData.pagesNum}"
      href="javascript:;" @click="getData(paginationData.current + 1)">
        <span class="material-icons d-block">
          chevron_right
        </span>
      </a>
    </li>
    <li>
      <a class="d-flex justify-content-center align-items-center"
      :class="{'disabled': paginationData.current === paginationData.pagesNum}"
      @click="getData(paginationData.pagesNum)"
      href="javascript:;">
        <span class="material-icons d-block">
          last_page
        </span>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  data() {
    return {
      paginationData: {
        pagesNum: 1,
        current: 1,
        pre: false,
        next: false,
        showPage: 4,
        pageGroup: 1,
        maxPageGroup: 1,
        omitBefore: false,
        omitAfter: false,
      },
    };
  },
  props: ['allDataNum'],
  emits: ['get-data', 'get-all-data'],
  methods: {
    getData(page = 1) {
      this.$emit('get-data', page);
      this.getPagination(page);
    },
    getPagination(page = 1) {
      console.log(this.allDataNum);
      this.paginationData.pagesNum = Math.ceil(this.allDataNum / 30);
      this.paginationData.current = page;
      this.paginationData.showPage = this.paginationData.pagesNum
      < 4 ? this.paginationData.pagesNum : 4;
      this.paginationData.pageGroup = Math.ceil(page / 4);
      this.paginationData.maxPageGroup = Math.ceil(this.paginationData.pagesNum / 4);
      this.paginationData.pre = this.paginationData.current !== 1;
      this.paginationData.next = this.paginationData.current < this.paginationData.pagesNum;
      if (page > this.paginationData.showPage) {
        this.paginationData.omitBefore = true;
      } else if (page <= this.paginationData.showPage) {
        this.paginationData.omitBefore = false;
      }
      if (this.paginationData.maxPageGroup !== this.paginationData.pageGroup) {
        this.paginationData.omitAfter = true;
      } else if (this.paginationData.maxPageGroup === this.paginationData.pageGroup) {
        this.paginationData.omitAfter = false;
      }
      if (this.paginationData.pagesNum % this.paginationData.showPage !== 0) {
        switch (this.paginationData.pageGroup) {
          case this.paginationData.maxPageGroup:
            this.paginationData.showPage = this.paginationData.pagesNum
            % this.paginationData.showPage;
            break;
          default:
            break;
        }
        console.log(this.paginationData.pagesNum % this.paginationData.showPage);
      }
    },
  },
};
</script>
